body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.tiles{
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: repeat(5, 1fr);
  grid-gap: 1px;
  background: #2f2f2f;
  height: 100vh;
}

.tile{
  position: relative;
}

.takeover{
  display: none;

  font-size: 1.2vmax;
    display: none;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    margin: 0;
    text-transform: uppercase;
    color: magenta;
    font-weight: 700;
    letter-spacing: 0.07em;
    text-decoration: underline;
    text-align: center;
    cursor: pointer;
}

.tile:hover .takeover{
  display: block;
}

@keyframes fadeIn{
  0% {opacity: 0}
  100% {opacity: 1}
}

.selectMedia{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 0, 255, 0.9);
  color: white;
  z-index: 2;
  animation: fadeIn 0.4s;
}

.selectMedia-close{
  position: absolute;
  top: 3vmax;
  right: 3vmax;
  cursor: pointer;
}

.selectMedia-title{
  margin: 0;
  font-size: 3vmax;
  font-weight: 400;
  margin-bottom: 2em;
}

.selectMedia-options{
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  text-decoration: underline;
  list-style: none;
  width: 80%;
  margin: 0;
}

.selectMedia-item{
  text-transform: uppercase;
  font-size: 1.3vmax;
  font-weight: 700;
  cursor: pointer;
  padding: 1em;
}

.selectMedia-item:hover{
  opacity: 0.5;
}